* {
  box-sizing: border-box;
  margin: 0;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

.wrap {
  width: 100%;
  height: 65px;
  background: #1b4194;
  padding-top: 15px;
  padding-bottom: 15px;
}

.search-form {
  width: 400px;
  height: 40px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

.search-form input {
  width: 360px;
  height: 36px;
  border: none;
  border-radius: 5px 0 0 5px;
  padding: 5px;
  position: absolute;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, .1), inset 0 1px 2px rgba(0, 0, 0, .3);
}

.search-form input:focus {
  outline: none;
}

.search-form button {
  width: 40px;
  height: 36px;
  text-align: center;
  color: #0c0a0a;
  cursor: pointer;
  background: #e9e9e9 url("https://fontawesomeicons.com/lib/svg/search-sharp.svg") 50% / 50% no-repeat;
  border: none;
  border-radius: 0 5px 5px 0;
  font-size: 20px;
  position: absolute;
  right: 0;
}

.search-form button:hover, .search-form button:focus {
  box-shadow: inset -6px -5px 13px -12px #ff2144;
}

.icon {
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
  display: inline-block;
}

.gallery {
  flex-wrap: wrap;
  gap: 15px;
  padding: 20px;
  display: flex;
}

.photo-card {
  border: 1px solid #eee;
  border-radius: 5px;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

img {
  width: 520px;
  height: 340px;
}

.info {
  justify-content: space-around;
  display: flex;
}

.info-item {
  text-align: center;
  margin: 5px;
}

.load-more {
  display: none;
}

.show {
  display: block;
}

