* {
  box-sizing: border-box;
  margin: 0;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

.wrap {
  width: 100%;
  height: 65px;
  background: rgb(27, 65, 148);
  padding-top: 15px;
  padding-bottom: 15px;
}

.search-form {
  margin-left: auto;
  margin-right: auto;
  width: 400px;
  height: 40px;
  position: relative;
}

.search-form input {
  position: absolute;

  width: 360px;
  border: none;
  padding: 5px;
  height: 36px;
  border-radius: 5px 0 0 5px;

  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.1),
    inset 0 1px 2px rgba(0, 0, 0, 0.3);
}

.search-form input:focus {
  outline: none;
}

.search-form button {
  position: absolute;
  right: 0;

  width: 40px;
  height: 36px;
  border: none;

  background: #e9e9e9;

  background-image: url('https://fontawesomeicons.com/lib/svg/search-sharp.svg');
  background-repeat: no-repeat;
  background-size: 50%;
  background-position: 50%;

  text-align: center;
  color: #0c0a0a;
  border-radius: 0 5px 5px 0;
  cursor: pointer;
  font-size: 20px;
}

.search-form button:hover,
.search-form button:focus {
  box-shadow: inset -6px -5px 13px -12px #ff2144;
}

.icon {
  display: inline-block;
  width: 1em;
  height: 1em;
  stroke-width: 0;
  stroke: currentColor;
  fill: currentColor;
}
.gallery {
  padding: 20px;
  display: flex;
  gap: 15px;
  flex-wrap: wrap;
}

.photo-card {
  border: 1px solid #eee;
  border-radius: 5px;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.12), 0px 1px 1px rgba(0, 0, 0, 0.14),
    0px 2px 1px rgba(0, 0, 0, 0.2);
}

img {
  width: 520px;
  height: 340px;
}

.info {
  display: flex;
  justify-content: space-around;
}

.info-item {
  margin: 5px;
  text-align: center;
}

.load-more {
  display: none;
}

.show {
  display: block;
}
